*{margin: 0;
padding: 0;
box-sizing: border-box;}

.state{
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  border: 2px solid #333;
  padding: 30px;
}

.state h1{
  font-size: 30px;
  margin-bottom: 5px;
}

.state input{
  border: none;
  background-color: #333;
  color: white;
  width: 80%;
  margin-right: 20px;
  height: 35px;
  font-size: 17px;
  padding: 0 10px;
font-weight: lighter;}


.state input::placeholder{
  color: white;
font-weight: lighter;}

  button{
    color: #333;
    width: 15%;
    height: 35px;
    border: 1px solid #333;
    font-size: 17px;
    background-color: white;
    margin-bottom: 10px;
  }

  p{
    font-size: 15px;
    border-bottom: 1px solid #333;
    margin-top: 10px;
    padding-bottom: 5px;
  }